import React from "react";
import PropTypes from 'prop-types';


import { FeedbackOptions, FeedbackItem, Counter, Percentage, Total} from './Statistics.styled';
  

export default function Statistics({options, handleTotal, onPosivePercent}) {

  const total = handleTotal();
  const positivePercentage = Number.parseInt(onPosivePercent(total));
  const notification = "No feedback given";
  
  if (total === 0) {
    return notification;
  } 
  return (
    <FeedbackOptions>
        
      <FeedbackItem>Good:
        <Counter>{options.good}</Counter></FeedbackItem>
      <FeedbackItem>Neutral:
        <Counter>{options.neutral}</Counter></FeedbackItem>
      <FeedbackItem>Bad:
        <Counter>{options.bad}</Counter></FeedbackItem>
        
      <FeedbackItem>Total: 
        <Total>{total}
        </Total>
      </FeedbackItem>
        
      <FeedbackItem>Positive feedback: 
        <Percentage>
          {positivePercentage >= 0
            ? positivePercentage
            : 0} %
        </Percentage>
      </FeedbackItem>
    </FeedbackOptions>
  )
}

Statistics.propTypes = {
  options: PropTypes.shape({
    good: PropTypes.number,
    neutral: PropTypes.number,
    bad: PropTypes.number,
  }),
  handleTotal: PropTypes.func,
  onPosivePercent: PropTypes.func,
}